.gifts-section-one {
  &__wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include tablets {
        display: block;
    }
  }
  &__container {
    max-width: 1160px;
  }
  &__column {
    width: 46%;
    &_pos_1 {
      .gifts-section-one__img {
        top: 20px;

      }


    }
    &_pos_2 {
      .gifts-section-one__img {
        @include laptop {
          top: 0;
          left: 0;
        }

      }


    }
    @include tablets {
      width: 100%;
    }
  }
  &__item {
    margin-bottom: 80px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    overflow: hidden;
    padding: 26px;
    background-color: #f6f6f6;
    min-height: 377px;
    @include laptop {
      min-height: 340px;
    }
    @include tablets {
      min-height: 400px;
      margin-bottom: 50px;
    }
    @include phones {
      min-height: 270px;
      margin-bottom: 20px;

    }

    
  }
  &__img {
    position: absolute;
    @include laptop {
      max-width: 85%;
    }
    @include tablets {
      left: initial ;
      right: 20px;
    }

  }
  &__footer {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
    position: relative;
  }
  &__name {
    color: $color_title_blue;
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;
    max-width: 400px;
    position: relative;
    @include phones {
      font-size: 18px;
      line-height: 18px;
      padding-top: 38%;
    }
  }

  &__text {
    color: #1d1d1d;
    font-size: 19px;
    line-height: 23px;
    max-width: 250px;
    @include phones {
      font-size: 12px;
      line-height: 12px;
    }
  }
  &__btn {
    padding-top: 11px;
    padding-bottom: 11px;
  }
}
